import { Injectable } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import {
  CustomerCreditsEntity,
  DontHaveApprovalAttemptWithDataException,
  InstallmentList,
  PreapprovedEntity,
  PreapprovedParams
} from '@nequi/features/credits/core';
import { toCamelCaseProperties } from '@nequi/utils/functions';

import { CreditInfoModel } from '../../services/credit-info.model';
import { ApprovalFrontViewModel } from '../approval-front/approval-front.view-model';
import { PreapprovalConfig } from './preapproval.config';

export interface InstallmentParams {
  name: string;
  value: number;
  amount: number;
}

@Injectable()
export class PreapprovalViewModel {
  public form: FormGroup;
  public financingAmount: FormControl;
  public term: FormControl;
  public currentTerm: InstallmentParams;
  public minAmount = 0;
  public maxAmount = 0;
  public customerCredits;
  public nationalGuaranteeFundCommissionPercentage = 0;
  public monthlyInterestRate = 0;
  public monthlyInsuranceAmount = 0;
  public insuranceCoverageAmount = 0;
  public loanAmount = 0;
  public isWrongTime = false;
  public userDontHavePreApproved = false;
  public termOptionsSelect: InstallmentParams[];
  public eventId: boolean | number;

  public details = [];
  public isMinAmountPreaproved = false;
  public preapproved: PreapprovedEntity;
  public operationParams: PreapprovedParams<number>;
  public isDataLoaded = false;
  public bussyIndicator = {
    show: false,
    error: '',
  };

  private config = PreapprovalConfig;

  constructor(
    private creditInfoModel: CreditInfoModel,
    public approvalFrontViewModel: ApprovalFrontViewModel
  ) {}

  public async initForm(amount: number): Promise<void> {
    this.financingAmount = new FormControl(amount ?? 0, [Validators.required]);
    this.term = new FormControl(0, [Validators.required]);
    this.form = new FormGroup({
      financingAmount: this.financingAmount,
      term: this.term,
    });
  }

  public get monthlyFee(): number {
    const valueTerm = Number(this.form.value.term.value);
    const p100 = 100;
    const nationalGuaranteeFundCommission = this.nationalGuaranteeFundCommissionPercentage / p100;

    return (
      ((this.financingAmount.value + this.financingAmount.value * nationalGuaranteeFundCommission) *
        ((1 + this.monthlyInterestRate) ** valueTerm * this.monthlyInterestRate)) /
        ((1 + this.monthlyInterestRate) ** valueTerm - 1) +
      Math.ceil(
        (this.financingAmount.value +
          this.financingAmount.value * nationalGuaranteeFundCommission) /
          this.insuranceCoverageAmount
      ) *
        this.monthlyInsuranceAmount
    );
  }

  public async processPreapproved(customerCredits: CustomerCreditsEntity) {
    this.customerCredits = customerCredits;

    await this.isCalledApprovalFront();
    this.preapproved = this.customerCredits.preapproved;

    this.setViewModel();
  }

  public async isCalledApprovalFront(): Promise<void> {
    if (
      this.approvalFrontViewModel.info.isFromApproval &&
      this.approvalFrontViewModel.info.installmentList?.length
    ) {
      const list = await this.sortInstallments<InstallmentList[]>(
        this.approvalFrontViewModel.info.installmentList,
        this.config.sortProperty.installment
      );

      this.customerCredits.preapproved.maxAmount = list[0].value;
      this.financingAmount.setValue(this.customerCredits.preapproved.maxAmount);

      if (
        this.approvalFrontViewModel.info.attempt.error &&
        this.approvalFrontViewModel.info.attempt.error.constructor.name ===
          DontHaveApprovalAttemptWithDataException.name
      ) {
        this.customerCredits.preapproved.maxInstallment = list[0].installment;
        this.customerCredits.preapproved.minInstallment = list[list.length - 1].installment;
        await this.setTermOptionsWithInstallment(list);

        return;
      }
    }

    this.setTermOptionSelect();
  }

  public setTermOptionSelect() {
    const data = [];

    for (
      let index = this.customerCredits.preapproved.maxInstallment;
      index >= this.customerCredits.preapproved.minInstallment;
      index--
    ) {
      data.push({
        name: `${index} ${this.operationParams.installmentSuffix}`,
        value: index,
        amount: this.customerCredits.preapproved.maxAmount,
      } as InstallmentParams);
    }

    this.termOptionsSelect = data ?? [];
  }

  public async setTermOptionsWithInstallment(data: InstallmentList[]) {
    this.termOptionsSelect = data.map((item: InstallmentList) => {
      return {
        name: `${item?.installment} ${this.operationParams.installmentSuffix}`,
        value: item?.installment,
        amount: item?.value,
      };
    });

    this.termOptionsSelect = await this.sortInstallments<InstallmentParams[]>(
      this.termOptionsSelect,
      this.config.sortProperty.installmentParams
    );
  }

  public setPaymentGuaranteesInfo() {
    this.creditInfoModel.creditFeatureInfo = {
      loanAmount: this.financingAmount.value,
      commissionPercentage: this.nationalGuaranteeFundCommissionPercentage,
      monthlyFee: this.monthlyFee,
      term: this.term.value,
      approvedDate: this.preapproved.approvedAt,
      installment: this.form.value?.term?.value ?? this.preapproved.maxInstallment,
      expirationDateText: this.preapproved.expirationDateText,
    };
  }

  public changeInstallment(event) {
    this.creditInfoModel.creditFeatureInfo = {
      installment: event.value,
    };

    this.maxAmount = event.amount;
    this.financingAmount.setValue(event.amount);
  }

  public buildDetail(messagesDetail: Record<string, string>) {
    const { label, how, when, help } = toCamelCaseProperties(messagesDetail);

    this.details = [
      {
        label,
        helpMessage: help,
      },
      {
        label: how,
      },
      {
        label: when,
        description: this.preapproved.expirationAt.toString(),
      },
    ];
  }

  public getMonthlyFee(): number {
    return this.monthlyFee;
  }

  private setViewModel() {
    let [term] = this.termOptionsSelect;

    if (
      this.approvalFrontViewModel.info.isFromApproval &&
      this.approvalFrontViewModel.info.installmentList?.length
    ) {
      term =
        this.termOptionsSelect.find(
          (item) => item.value === this.approvalFrontViewModel.info.installmentList[0].installment
        ) ?? term;
    }

    this.term.setValue(term);
    this.minAmount = this.preapproved.minAmount
      ? this.preapproved.minAmount
      : this.operationParams.baseAmount;

    this.maxAmount = this.preapproved.maxAmount;

    this.nationalGuaranteeFundCommissionPercentage = this.preapproved.percentageOfCommission;
    this.monthlyInterestRate = this.operationParams.monthlyInterestRate;
    this.monthlyInsuranceAmount = this.operationParams.monthlyInsurance;
    this.insuranceCoverageAmount = this.operationParams.insuranceCoverage;
    this.isMinAmountPreaproved = this.preapproved.minAmount === this.preapproved.maxAmount;

    this.creditInfoModel.creditFeatureInfo = {
      approvedDate: this.preapproved.approvedAt,
      installment: this.preapproved.maxInstallment,
    };

    this.creditInfoModel.agreement = {
      agreement: this.preapproved.product.agreement,
      agreementFGA: this.preapproved.product.secondaryAgreement,
    };

    this.isDataLoaded = true;
  }

  private sortInstallments<T>(data, property): Promise<T> {
    return data.sort((a, b) => {
      if (a[property] < b[property]) {
        return 1;
      }

      if (a[property] > b[property]) {
        return -1;
      }

      return 0;
    });
  }
}
