import { CurrencyPipe, PercentPipe } from '@angular/common';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { NavController } from '@ionic/angular';
import { ModuleConfigProvider, TranslateProvider } from '@nequi/features/commons/providers';

import { CreditInfoModel } from '../../services/credit-info.model';
import { ContractType } from './constants/contract-type';
import { ContractConfig } from './contract.config';

@Component({
  selector: 'app-contract-organism',
  templateUrl: './contract.organism.html',
  styleUrls: ['./contract.organism.scss'],
})
export class ContractOrganism implements OnInit {
  @Input() public type: ContractType;
  @Input() public agreement: string;
  @Input() public messages: Record<string, unknown>;
  @Output() public accepted = new EventEmitter<boolean>();

  public config = ContractConfig;
  public acceptAgreementCheckbox: FormControl;

  constructor(
    private translateProvider: TranslateProvider,
    private navController: NavController,
    private creditInfoModel: CreditInfoModel,
    private currencyPipe: CurrencyPipe,
    private moduleConfigProvider: ModuleConfigProvider,
    private percentPipe: PercentPipe
  ) {}

  public async ngOnInit() {
    this.initForm();
    await this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
    this.initPage();
  }

  public goBack() {
    this.navController.back();
  }

  public acceptAgreement() {
    if (this.acceptAgreementCheckbox.value) {
      this.accepted.emit(true);
    }
  }

  private initForm(): void {
    this.acceptAgreementCheckbox = new FormControl(null, [Validators.required]);
  }

  private async initPage() {
    if (!this.messages) {
      this.messages = this.translateProvider.get(this.config.i18n[this.type]);
    }

    if (!this.agreement) {
      this.agreement = this.creditInfoModel.agreement.agreement;
      this.replaceVariable();
    }
  }

  private replaceVariable() {
    const contractHTML = this.agreement;
    const installmentDay = '';
    const installmentPeriods = 0;
    const installmetMinusThree = 0;
    const commissionPercentage = this.creditInfoModel.creditFeatureInfo.commissionPercentage;

    const installmentValue = this.currencyPipe.transform(
      this.creditInfoModel.creditFeatureInfo.monthlyFee
    );
    const approvedValue = this.currencyPipe
      .transform(this.creditInfoModel.creditFeatureInfo.loanAmount)
      .replace(this.config.replaceString.space, '');

    const insuranceValue = this.currencyPipe
      .transform(this.creditInfoModel.loanPortfolioInfo.monthlyInsuranceAmount)
      .replace(this.config.replaceString.space, '');

    const installmentNum = this.currencyPipe.transform(
      this.creditInfoModel.creditFeatureInfo.installment
    );
    const totalValue = this.currencyPipe.transform(
      this.creditInfoModel.creditFeatureInfo.loanAmount +
        this.creditInfoModel.creditFeatureInfo.monthlyFee +
        this.creditInfoModel.loanPortfolioInfo.monthlyInsuranceAmount
    );
    const installment = this.currencyPipe
      .transform(
        this.creditInfoModel.creditFeatureInfo.loanAmount +
          this.creditInfoModel.creditFeatureInfo.monthlyFee
      )
      .replace(this.config.replaceString.space, '');
    const monthlyInterestRate = this.percentPipe
      .transform(
        this.creditInfoModel.creditFeatureInfo.monthlyInterestRate,
        this.config.pipeFormat.percent
      )
      .replace(this.config.replaceString.space, '');

    const expirationDateText = this.creditInfoModel.creditFeatureInfo.expirationDateText.replace(
      this.config.replaceString.repeatedText,
      ''
    );

    this.agreement = contractHTML
      .replace(this.config.replaceString.insuranceValue, insuranceValue)
      .replace(this.config.replaceString.value, approvedValue)
      .replace(this.config.replaceString.totalValue, totalValue)
      .replace(this.config.replaceString.installmentValue, installmentValue)
      .replace(this.config.replaceString.numberInstallments, installmentNum)
      .replace(this.config.replaceString.installmentDay, installmentDay)
      .replace(this.config.replaceString.installmentDay, String(installmentPeriods))
      .replace(this.config.replaceString.installmentDay, String(installmetMinusThree))
      .replace(this.config.replaceString.monthlyInterestRate, monthlyInterestRate)
      .replace(this.config.replaceString.annualEffectiveRate, String(commissionPercentage))
      .replace(this.config.replaceString.paymentDate, expirationDateText)
      .replace(this.config.replaceString.disbursementValue, approvedValue)
      .replace(this.config.replaceString.installment, installment)
      .replace(this.config.replaceString.insurance, insuranceValue)
      .replace(this.config.replaceString.nextPaymentDate, expirationDateText)
      .replace(this.config.replaceString.disbursement, approvedValue)
      .replace(this.config.replaceString.singleValuePaymentFee, installment);
  }
}
