import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { YourMoneyEngineEntity } from '@nequi/features/credits/core';

import { YourMoneyEngineService } from '../../services/your-money-engine.service';
import { YourMoneyEngineConfig } from './your-money-engine.config';
import { YourMoneyEngineViewModel } from './your-money-engine.view-model';

@Component({
  selector: 'app-your-money-engine-credits',
  templateUrl: './your-money-engine.page.html',
  styleUrls: ['./your-money-engine.page.scss'],
})
export class YourMoneyEnginePage extends FeatureBasePage implements OnInit {
  public viewModel: YourMoneyEngineViewModel;
  public config = YourMoneyEngineConfig;
  public yourMoneyData;

  constructor(
    protected readonly injector: Injector,
    private router: Router,
    private yourMoneyEngineService: YourMoneyEngineService
  ) {
    super(injector);
    this.viewModel = new YourMoneyEngineViewModel();
  }

  public ngOnInit() {
    this.initPage();
  }

  public onComplete(): void {
    this.yourMoneyEngineService.emit(this.viewModel.form.value);
  }

  private async initPage(): Promise<void> {
    const navigationExtras = this.router.getCurrentNavigation().extras.state;

    this.yourMoneyData = navigationExtras.yourMoneyData as YourMoneyEngineEntity;

    this.viewModel.setCeilings(this.config.ceilings.min, this.config.ceilings.max);
    this.viewModel.initForm(this.yourMoneyData, navigationExtras.canShowAdditionalIncomesField);
  }
}
