import { AbstractControl, ValidatorFn } from '@angular/forms';

export class CustomValidatorsForm {
  public maxOverdueFeeValidator(overdueFee: number): ValidatorFn {
    return (control: AbstractControl): { [key: string]: boolean } | null => {
      if (control.value > overdueFee) {
        return { maxOverdueFee: true };
      }

      return null;
    };
  }

  public maxFeeValidator(maxPaymentValue: number): ValidatorFn {
    return (control: AbstractControl): { [key: string]: boolean } | null => {
      if (control.value > maxPaymentValue) {
        return { maxFee: true };
      }

      return null;
    };
  }
}
