import { CurrencyPipe, DatePipe } from '@angular/common';
import { Injectable } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ModuleConfigProvider } from '@nequi/features/commons';
import { CreditEntity } from '@nequi/features/credits/core';
import { Pocket } from '@nequi/features/money-management/core';
import { PresentationPocket } from '@nequi/features/money-management/presentation';
import { ConfirmInfoPocket, ConfirmInfoPocketItem, CurrencyConfigProvider } from '@nequi/ui';
import { createFormat } from '@nequi/utils/string';

import { PaymentService } from '../../services/payment.service';
import { CustomValidatorsForm } from './custom-validators';
import { PaymentType } from './payment.type';

@Injectable()
export class PaymentViewModel {
  public form: FormGroup;
  public isContentVisible = true;
  public paymentControl: FormControl;
  public payFeeControl: FormControl;
  public payCompleteControl: FormControl;
  public loanInstallments;

  public chip = {
    isVisible: false,
    text: '',
    class: '',
  };

  public credit: CreditEntity;
  public installmentPending: number;
  public lastPayment: Date;
  public nextPaymentAmount: number;
  public overdueFee: number;
  public selectedPayType: PaymentType;
  public feePaymentMessage: string;
  public paymentText = {
    message: '',
    placeholder: '',
  };

  public paymentTime: PaymentType;
  public paymentAmount: number;
  public pocketControl: FormControl;
  public pocketList: Pocket[];
  public errorMessages;
  public config;
  public normalInterest: number;
  public penaltyInterest: number;
  public principalPending: number;
  public fees: number;
  public clickStatus = true;

  public decimalLimitValue: number;

  private customValidators;

  constructor(
    private currencyPipe: CurrencyPipe,
    private paymentService: PaymentService,
    private currencyConfigProvider: CurrencyConfigProvider,
    private moduleConfigProvider: ModuleConfigProvider
  ) {
    this.customValidators = new CustomValidatorsForm();
  }

  public setConfig(_config: object) {
    this.config = _config;
  }

  public setCreditInfo() {
    this.installmentPending = this.credit.installment.totalPending;
    this.paymentTime = this.validateDate();
    this.overdueFee = this.getExpiredFeesValue();
    this.lastPayment = this.credit.endAt;
  }

  public initForm() {
    this.paymentControl = new FormControl('', this.validatePayTime());
    this.payFeeControl = new FormControl(false, []);
    this.payCompleteControl = new FormControl(false, []);
    this.pocketControl = new FormControl(null, Validators.required);
    this.form = new FormGroup({
      paymentControl: this.paymentControl,
      payTodayFeeControl: this.payFeeControl,
      payCompleteControl: this.payCompleteControl,
    });
  }

  public buildItems(
    messages: Record<string, any>,
    pocketInfo: PresentationPocket,
    currencyPipe: CurrencyPipe,
    datePipe: DatePipe
  ): Array<ConfirmInfoPocketItem | ConfirmInfoPocket> {
    const pocket: ConfirmInfoPocket = {
      isPocket: true,
      title: messages.items.pocket,
      name: pocketInfo.name,
      image: pocketInfo.imageUrl,
      icon: pocketInfo.iconUrl,
    };
    let items: Array<ConfirmInfoPocketItem | ConfirmInfoPocket> = [];

    if (this.paymentTime === PaymentType.PAY_TODAY_FEE) {
      items = this.buildPayTodayItems(messages, items, currencyPipe);
    }

    if (this.paymentTime === PaymentType.OVERDUE_FEE) {
      items = this.buildPayOverdueItems(messages, items, currencyPipe);
    }

    if (this.paymentTime === PaymentType.PAYMENT) {
      items = this.buildTotalPayItems(messages, items, currencyPipe, datePipe);
    }

    return [...items, pocket];
  }

  public checkPayFee() {
    if (!this.clickStatus) {
      return;
    }

    this.clickStatus = false;

    if (this.paymentTime === PaymentType.PAY_TODAY_FEE) {
      this.checkPayFeeStatus(
        {
          message: this.config.i18n.payment.payTodayFeeHelpUnchecked,
          placeholder: this.config.i18n.payment.paySomething,
        },
        {
          message: this.config.i18n.payment.payTodayFeeHelpChecked,
          placeholder: this.config.i18n.payment.payTodayFee,
        },
        PaymentType.PAY_TODAY_FEE,
        this.credit.installment.next.value
      );

      return;
    }

    if (this.paymentTime === PaymentType.OVERDUE_FEE) {
      this.checkPayFeeStatus(
        {
          message: this.config.i18n.payment.payTodayFeeHelpUnchecked,
          placeholder: this.config.i18n.payment.paySomething,
        },
        {
          message: this.config.i18n.payment.payOverdueFeeHelpChecked,
          placeholder: this.config.i18n.payment.payOverdueFee,
        },
        PaymentType.OVERDUE_FEE,
        this.overdueFee
      );
    }
  }

  public checkPayComplete() {
    if (!this.clickStatus) {
      return;
    }

    this.clickStatus = false;

    if (this.paymentTime === PaymentType.PAYMENT) {
      this.checkPayCompleteStatus(
        {
          message: this.config.i18n.payment.paymentHelp,
          placeholder: this.config.i18n.payment.payment,
        },
        {
          message: '',
          placeholder: this.config.i18n.payment.payment,
        }
      );

      return;
    }

    if (this.paymentTime === PaymentType.PAY_TODAY_FEE) {
      this.checkPayCompleteStatus(
        {
          message: this.config.i18n.payment.payTodayFeeHelpUnchecked,
          placeholder: this.config.i18n.payment.paySomething,
        },
        {
          message: '',
          placeholder: this.config.i18n.payment.paySomething,
        }
      );

      return;
    }

    if (this.paymentTime === PaymentType.OVERDUE_FEE) {
      this.checkPayCompleteStatus(
        {
          message: this.config.i18n.payment.payTodayFeeHelpUnchecked,
          placeholder: this.config.i18n.payment.paySomething,
        },
        {
          message: this.config.i18n.payment.payTodayFeeHelpUnchecked,
          placeholder: this.config.i18n.payment.paySomething,
        }
      );
    }
  }

  public async getPageConfiguration(errorMessage: any, i18nOverdueFee: string) {
    this.errorMessages = {
      required: '',
      min: errorMessage.minFee,
      max: errorMessage.maxTotalPayment,
      maxFee: createFormat(errorMessage.maxFee, [
        this.currencyPipe.transform(this.credit.maxPaymentValue),
      ]),
    };

    if (this.paymentTime === PaymentType.PAY_TODAY_FEE) {
      this.selectedPayType = PaymentType.PAY_ALL;
      this.chip.isVisible = true;
      this.chip.text = this.config.i18n.chip.payTodayFee;
      this.chip.class = this.config.chipsClass.yellow;
      this.checkPayFee();
      this.paymentService.showPayTodayModal(
        this.credit.installment.next.date,
        this.config.i18n.toast.payToday,
        this.config.i18n.modal.payTodayFee,
        this.credit.installment.next.value
      );

      this.feePaymentMessage = this.config.i18n.payment.payTodayFee;

      return;
    }

    if (this.paymentTime === PaymentType.OVERDUE_FEE) {
      this.selectedPayType = PaymentType.PAY_ALL;
      this.chip.isVisible = true;
      this.chip.text = this.config.i18n.chip.overdueFee;
      this.chip.class = this.config.chipsClass.red;
      this.checkPayFee();
      this.paymentService.showModalOverdueFee(i18nOverdueFee, this.installmentPending);

      this.feePaymentMessage = this.config.i18n.payment.payOverdueFee;

      this.errorMessages = {
        required: '',
        min: errorMessage.minFee,
        max: errorMessage.maxTotalPayment,
        maxFee: createFormat(errorMessage.maxFee, [
          this.currencyPipe.transform(this.credit.maxPaymentValue),
        ]),
        maxOverdueFee: createFormat(errorMessage.maxOverdueFee, [
          this.currencyPipe.transform(this.overdueFee),
        ]),
      };

      return;
    }

    this.paymentService.showFirstTimeMessage(
      this.moduleConfigProvider.applicationData.homeIsShowFirstMessage,
      this.config.i18n.toast.payment
    );

    this.paymentTime = PaymentType.PAYMENT;
    this.selectedPayType = PaymentType.PAYMENT;
    this.chip.isVisible = false;
    this.chip.text = '';
    this.chip.class = '';
    this.paymentText = {
      message: this.config.i18n.payment.paymentHelp,
      placeholder: this.config.i18n.payment.payment,
    };

    this.paymentControl.setValue(0);
  }

  private checkPayCompleteStatus(uncheckText, checkText) {
    if (this.selectedPayType === PaymentType.PAY_ALL) {
      this.uncheckPaymentStatus(true, uncheckText);

      return;
    }

    this.checkPaymentStatus(true, checkText, PaymentType.PAY_ALL, this.credit.balance);
  }

  private checkPayFeeStatus(uncheckText, checkText, paymentType, amount) {
    if (
      this.selectedPayType === PaymentType.PAY_TODAY_FEE ||
      this.selectedPayType === PaymentType.OVERDUE_FEE
    ) {
      this.uncheckPaymentStatus(false, uncheckText);

      return;
    }

    this.checkPaymentStatus(false, checkText, paymentType, amount);
  }

  private uncheckPaymentStatus(hasPayComplete, texts) {
    this.paymentText = texts;
    this.selectedPayType = PaymentType.PAYMENT;

    if (hasPayComplete) {
      this.payCompleteControl.setValue(false);
    } else {
      this.payFeeControl.setValue(false);
    }

    this.paymentControl.enable();
    this.paymentControl.setValue(0);
    this.paymentAmount = 0;
    this.clickStatus = true;
    this.decimalLimitValue = this.currencyConfigProvider.decimalLimit;

    this.form.markAsPristine();
    this.form.markAsUntouched();
  }

  private checkPaymentStatus(hasPayComplete, texts, payType, amount) {
    this.paymentText = texts;
    this.selectedPayType = payType;

    if (hasPayComplete) {
      this.payCompleteControl.setValue(true);
    } else {
      this.payFeeControl.setValue(true);
    }

    this.paymentControl.disable();
    this.paymentControl.setValue(amount);
    this.paymentAmount = amount;
    this.decimalLimitValue = this.currencyConfigProvider.visualDecimalLimit;

    if (hasPayComplete) {
      this.payFeeControl.setValue(false);
    } else {
      this.payCompleteControl.setValue(false);
    }

    this.clickStatus = true;

    this.form.markAsPristine();
    this.form.markAsUntouched();
  }

  private validatePayTime() {
    if (this.paymentTime === PaymentType.OVERDUE_FEE) {
      return [
        Validators.required,
        Validators.min(1),
        Validators.max(this.credit.balance),
        this.customValidators.maxFeeValidator(this.credit.maxPaymentValue),
        this.customValidators.maxOverdueFeeValidator(this.overdueFee),
      ];
    }

    return [
      Validators.required,
      Validators.min(1),
      Validators.max(this.credit.balance),
      this.customValidators.maxFeeValidator(this.credit.maxPaymentValue),
    ];
  }

  private buildPayTodayItems(
    messages: Record<string, any>,
    items: Array<ConfirmInfoPocketItem | ConfirmInfoPocket>,
    currencyPipe: CurrencyPipe
  ): Array<ConfirmInfoPocketItem | ConfirmInfoPocket> {
    items.push({
      label: messages.items.amount,
      value: currencyPipe.transform(this.paymentAmount),
    });

    const { total, totalPaid } = this.credit.installment;

    if (this.selectedPayType === PaymentType.PAYMENT) {
      items.push(
        {
          label: messages.items.interestNormal.label,
          value: currencyPipe.transform(this.normalInterest),
        },
        {
          label: messages.items.installmentsToPay,
          value: String(total - totalPaid),
        }
      );
    }

    if (this.selectedPayType === PaymentType.PAY_TODAY_FEE) {
      this.fees = this.principalPending;

      items.push(
        {
          label: messages.items.insurance.label,
          value: currencyPipe.transform(this.credit.feesOutstanding),
          helpMessage: createFormat(messages.items.insurance.message, [
            this.currencyPipe.transform(this.credit.loanDemandOutstandingDetails.fees),
          ]),
        },
        {
          label: messages.items.interestArrears.label,
          value: currencyPipe.transform(this.penaltyInterest),
          helpMessage: messages.items.interestArrears.message,
        },
        {
          label: messages.items.interestNormal.label,
          value: currencyPipe.transform(this.normalInterest),
        },
        {
          label: messages.items.capitalToPay.label,
          value: currencyPipe.transform(this.fees),
          helpMessage: messages.items.capitalToPay.message,
        },
        {
          label: messages.items.loanDebt,
          value: currencyPipe.transform(this.credit.balance),
        },
        {
          label: messages.items.installmentsToPay,
          value: String(total - (totalPaid + 1)),
        }
      );
    }

    if (this.selectedPayType === PaymentType.PAY_ALL) {
      items.push(
        {
          label: messages.items.loanDebt,
          value: currencyPipe.transform(this.credit.balance),
        },
        {
          label: messages.items.interestNormalPayAll.label,
          value: currencyPipe.transform(this.normalInterest),
          helpMessage: messages.items.interestNormalPayAll.message,
        }
      );
    }

    return items;
  }

  private buildPayOverdueItems(
    messages: Record<string, any>,
    items: Array<ConfirmInfoPocketItem | ConfirmInfoPocket>,
    currencyPipe: CurrencyPipe
  ): Array<ConfirmInfoPocketItem | ConfirmInfoPocket> {
    const amountToPayItem: ConfirmInfoPocketItem = {
      label: messages.items.amount,
      value: currencyPipe.transform(this.paymentAmount),
    };

    const { total, totalPaid } = this.credit.installment;

    if (this.selectedPayType === PaymentType.PAYMENT) {
      items.push(
        amountToPayItem,
        {
          label: messages.items.loanDebt,
          value: currencyPipe.transform(this.credit.balance),
        },
        {
          label: messages.items.installmentsToPay,
          value: String(total - totalPaid),
        }
      );
    }

    if (this.selectedPayType === PaymentType.OVERDUE_FEE) {
      items.push(
        amountToPayItem,
        {
          label: messages.items.insuranceToPay.label,
          value: currencyPipe.transform(this.credit.feesOutstanding),
          helpMessage: createFormat(messages.items.insuranceToPay.message, [
            this.currencyPipe.transform(this.credit.loanDemandOutstandingDetails.fees),
          ]),
        },
        {
          label: messages.items.interestArrears.labelOverdue,
          value: currencyPipe.transform(this.penaltyInterest),
          helpMessage: messages.items.interestArrears.message,
        },
        {
          label: messages.items.interestNormal.labelOverdue,
          value: currencyPipe.transform(this.normalInterest),
          helpMessage: messages.items.interestNormal.message,
        },
        {
          label: messages.items.loanDebt,
          value: currencyPipe.transform(this.credit.balance),
        },
        {
          label: messages.items.installmentsToPay,
          value: String(total - (totalPaid + 1)),
        }
      );
    }

    if (this.selectedPayType === PaymentType.PAY_ALL) {
      items.push(
        {
          label: messages.items.loanDebt,
          value: currencyPipe.transform(this.credit.balance),
        },
        {
          label: messages.items.interestNormal.labelOverdue,
          value: currencyPipe.transform(this.normalInterest),
          helpMessage: messages.items.interestNormal.message,
        },
        {
          label: messages.items.interestArrears.labelOverdue,
          value: currencyPipe.transform(this.penaltyInterest),
          helpMessage: messages.items.interestArrears.message,
        },
        {
          label: messages.items.insuranceToPay.label,
          value: currencyPipe.transform(this.credit.feesOutstanding),
          helpMessage: createFormat(messages.items.insuranceToPay.message2, [
            this.currencyPipe.transform(this.credit.loanDemandOutstandingDetails.fees),
          ]),
        },
        amountToPayItem
      );
    }

    return items;
  }

  private buildTotalPayItems(
    messages: Record<string, any>,
    items: Array<ConfirmInfoPocketItem | ConfirmInfoPocket>,
    currencyPipe: CurrencyPipe,
    datePipe: DatePipe
  ): Array<ConfirmInfoPocketItem | ConfirmInfoPocket> {
    const amountToPayItem: ConfirmInfoPocketItem = {
      label: messages.items.amount,
      value: currencyPipe.transform(this.paymentAmount),
    };

    const { normalInterest, penaltyInterest } = this.credit;

    items.push(amountToPayItem, {
      label: messages.items.loanDebt,
      value: currencyPipe.transform(normalInterest),
    });

    if (this.selectedPayType === PaymentType.PAY_ALL) {
      items.push(
        {
          label: messages.items.interestNormalPayAll.label,
          helpMessage: messages.items.interestNormalPayAll.message,
          value: currencyPipe.transform(penaltyInterest),
        },
        {
          label: messages.items.insuranceToPay.label,
          value: currencyPipe.transform(this.credit.feesOutstanding),
        }
      );
    }

    items.push({
      label: messages.items.date,
      value: datePipe.transform(new Date(this.credit.endAt.toString())),
    });

    return items;
  }

  private validateDate() {
    const today = new Date();
    const nextInstallmentDate = this.credit.installment.next.date;

    today.setHours(0, 0, 0, 0);
    nextInstallmentDate.setHours(0, 0, 0, 0);

    if (this.credit.isExpired) {
      return PaymentType.OVERDUE_FEE;
    }

    if (today.getTime() === nextInstallmentDate.getTime()) {
      return PaymentType.PAY_TODAY_FEE;
    }

    return PaymentType.PAYMENT;
  }

  private getExpiredFeesValue() {
    this.fees = this.credit.feesOutstanding;

    if (this.credit.installment.total !== this.credit.totDmds) {
      this.fees = this.credit.loanDemandOutstandingDetails.fees;
    }

    this.normalInterest = this.credit.loanDemandOutstandingDetails.normalInterest;
    this.penaltyInterest = this.credit.loanDemandOutstandingDetails.penaltyInterest;
    this.principalPending = this.credit.loanDemandOutstandingDetails.principalPending;

    return this.fees + this.normalInterest + this.penaltyInterest + this.principalPending;
  }
}
