import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { CoreException } from '@nequi/commons/exceptions';
import {
  AnalyticsInteractor,
  AnalyticsType,
  BarcodeInformationEntity,
  BarcodeStatus,
  BarcodeStatusEntity,
  DocumentScanInteractor,
  LoggerInteractor,
  ModuleCode,
  ModuleName
} from '@nequi/features/commons/core';
import { AlertService, DynatraceService } from '@nequi/features/commons/presentation';
import {
  I18nMessages,
  NavigatorProvider,
  SessionProvider,
  UserInfoProvider
} from '@nequi/features/commons/providers';
import {
  CreditInteractor,
  CreditType,
  DontHaveApprovalAttemptException,
  DontHaveApprovalAttemptWithDataException,
  HadObligationsInMoraException,
  HaveApprovalAttemptException,
  InstallmentList,
  ModuleCodeEngine,
  OccupationEngineEntity,
  SaveCustomerInfoParam,
  TimeoutApprovalFrontException,
  ValidateDataDoesNotMatchException,
  WrongDontComplyWithRiskPoliciesException,
  WrongDontUserInCentralsException,
  WrongMeetsPaymentCapacityException,
  YourMoneyEngineEntity
} from '@nequi/features/credits/core';
import { DocumentType } from '@nequi/features/lite-registry/core';
import { ModuleInfoInteractor } from '@nequi/features/profile/core';
import { ConfigModule, DocumentScanService } from '@nequi/features/profile/presentation';
import { CardModalService, LoadingService, ToastService } from '@nequi/ui';
import { DateFormat, transformDateFromString } from '@nequi/utils/date';
import {
  openUrl,
  stringsArrayToObjectWithValues,
  toCamelCaseProperties
} from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { CreditInfoModel } from '../../services/credit-info.model';
import { ApprovalFrontConfig } from './approval-front.config';
import { ApprovalFrontViewModel, EngineModuleItemList } from './approval-front.view-model';
import { ApprovalFrontModule } from './constants/approval-front-module';
import { ApprovalFrontService } from './services/approval-front.service';
import { OccupationEngineService } from './services/occupation-engine.service';
import { YourMoneyEngineService } from './services/your-money-engine.service';

@Component({
  selector: 'app-approval-front',
  templateUrl: './approval-front.organism.html',
  styleUrls: ['./approval-front.organism.scss'],
})
export class ApprovalFrontOrganism implements OnInit, OnDestroy {
  @Input() public organismId: string;
  @Input() public messages: I18nMessages<string>;
  @Output() public nextButtonCliked = new EventEmitter();
  @Output() public goBackCliked = new EventEmitter();
  @Output() public completed = new EventEmitter<SaveCustomerInfoParam>();

  public config = ApprovalFrontConfig;
  public factoryException = {};
  public i18n = null;

  constructor(
    private creditInteractor: CreditInteractor,
    public viewModel: ApprovalFrontViewModel,
    private creditInfoModel: CreditInfoModel,

    private userInfoProvider: UserInfoProvider,
    private loadingService: LoadingService,
    private loggerInteractor: LoggerInteractor,
    private navigatorProvider: NavigatorProvider,
    private documentScanService: DocumentScanService,
    private occupationEngineService: OccupationEngineService,
    private yourMoneyEngineService: YourMoneyEngineService,
    private documentScanInteractor: DocumentScanInteractor,
    private toastService: ToastService,
    public cardModalService: CardModalService,
    private approvalFrontService: ApprovalFrontService,
    private analyticsInteractor: AnalyticsInteractor,
    private moduleInfoInteractor: ModuleInfoInteractor,
    private alertService: AlertService,
    private dynatraceService: DynatraceService,
    private sessionProvider: SessionProvider
  ) {}

  public ngOnInit() {
    this.i18n = toCamelCaseProperties(this.messages);
    this.setExceptionFactory();
    this.getModules();
    this.setAnalyticsIntentionOfBegin();
  }

  public async ngOnDestroy() {
    await this.dynatraceService.setAnalyticsOfEnd();

    this.documentScanService.unsubscribe();
    this.occupationEngineService.unsubscribe();
    this.yourMoneyEngineService.unsubscribe();
  }

  public goTo(item: EngineModuleItemList) {
    switch (item.id) {
      case ApprovalFrontModule.YOUR_DOCUMENT:
        this.subscribeToDocumentScan(item as EngineModuleItemList<ConfigModule>);
        break;
      case ApprovalFrontModule.WHAT_DO_YOU_DO:
        this.subscribeToOccupation(item);
        break;
      case ApprovalFrontModule.YOUR_MONEY:
        this.subscribeToYourMoney(item);
        break;
    }
  }

  public async onValidateEngine(): Promise<void> {
    try {
      await this.loadingService.show();

      this.viewModel.info.isFromApproval = true;
      this.viewModel.info.attempt.isCallService = false;

      const term = this.creditInfoModel?.creditFeatureInfo?.installment ?? null;

      const installments = (await (this.creditInteractor.getApprovalFront({
        creditType: CreditType.PROPELLER,
        term: this.viewModel.info.currentInstallment.value ?? term,
        amount: this.creditInfoModel?.customerCreditInfo?.preapproved?.maxAmount ?? null,
      }) as unknown)) as InstallmentList[];

      await this.setEngineInstallments(installments);
      await this.loadingService.hide(true);

      this.onSuccesEmit();
    } catch (error) {
      await this.loadingService.hideWithoutFeedback();

      if (error?.data) {
        await this.setEngineInstallments(error.data);
      }

      await this.onFailGetApprovalFront(error);
    }
  }

  public goBack() {
    this.goBackCliked.emit();
  }

  public goToHelp() {
    openUrl(this.config.routes.help);
  }

  public finish(): void {
    this.getModules();

    this.navigatorProvider.navigateRoot(this.config.routes.currentUrl);
  }

  private onSuccesEmit(error?: CoreException) {
    this.setAnalyticsIntentionOfSucces(error);

    this.approvalFrontService.emit(true);
    this.nextButtonCliked.emit();
  }

  private async setEngineInstallments(installmentList: InstallmentList[]) {
    if (Array.isArray(installmentList) && installmentList?.length) {
      this.viewModel.info.installmentList = installmentList;
    }
  }

  private async onFailGetApprovalFront(error: CoreException) {
    if (this.factoryException[error.constructor.name]) {
      this.viewModel.info.attempt.error = error;
      this.factoryException[error.constructor.name](error);

      return;
    }

    this.dynatraceService.setCustomError(error);
    await this.toastService.showError(error.message);
  }

  private async getModules() {
    try {
      await this.loadingService.show();
      this.viewModel.modules.documentScan = await this.onValidateDocumentScanned();
      const data = await this.creditInteractor.getModules();

      await this.viewModel.loadModules(data);
    } catch (error) {
      await this.viewModel.loadModules(error?.data ?? []);
    }

    await this.loadingService.hideWithoutFeedback();
  }

  private subscribeToDocumentScan(item: EngineModuleItemList<ConfigModule>) {
    this.documentScanService.unsubscribe();

    this.documentScanService.configModule = {
      type: this.userInfoProvider.getDocument().type as DocumentType,
      phoneNumber: this.userInfoProvider.getPhoneNumber(),
      ...item.config,
    };

    this.documentScanService.subscribe(this.onDocumentScanCompleted.bind(this));

    this.navigatorProvider.navigateForward([item.link], {
      state: this.viewModel.modules.barcodeInfo,
    });
  }

  private subscribeToOccupation(item: EngineModuleItemList) {
    this.occupationEngineService.unsubscribe();
    this.occupationEngineService.subscribe(this.onOccupationCompleted.bind(this));

    this.navigatorProvider.navigateForward([item.link], {
      state: this.viewModel.modules.occupationData,
    });
  }

  private subscribeToYourMoney(item: EngineModuleItemList) {
    this.yourMoneyEngineService.unsubscribe();
    this.yourMoneyEngineService.subscribe(this.onYourMoneyCompleted.bind(this));

    const canShowAdditionalIncomesField = this.showAdditionalIncomes();

    this.navigatorProvider.navigateForward([item.link], {
      state: {
        yourMoneyData: this.viewModel.modules.yourMoneyData,
        canShowAdditionalIncomesField,
      },
    });
  }

  private showAdditionalIncomes() {
    return !(
      this.viewModel.modules.occupationData.occupation.code ===
        this.config.additionalIncomes.retired ||
      this.viewModel.modules.occupationData.occupation.code ===
        this.config.additionalIncomes.independent
    );
  }

  private async onDocumentScanCompleted(response: {
    data: BarcodeStatusEntity;
    barcodeInfo: BarcodeInformationEntity;
  }) {
    await this.loadingService.show();

    try {
      this.viewModel.modules.documentScan.data = response.data;
      this.viewModel.modules.barcodeInfo = response.barcodeInfo;
      this.documentScanService.configModule = null;

      await this.moduleInfoInteractor.saveModuleInfo<BarcodeInformationEntity>({
        code: ModuleCode.BARCODE_INFORMATION,
        data: { key: ModuleName.BARCODE_INFORMATION, value: this.viewModel.modules.barcodeInfo },
      });
    } catch (error) {
      this.loggerInteractor.error(error);
    }

    await this.loadingService.hideWithoutFeedback();

    this.completed.emit({
      moduleId: ModuleCodeEngine.BARCODE_INFORMATION,
      value: this.viewModel.modules.barcodeInfo,
    });
  }

  private async onOccupationCompleted(occupation: OccupationEngineEntity): Promise<void> {
    await this.loadingService.show();
    let occupationValue;

    try {
      occupation.sinceDate =
        typeof occupation.sinceDate === 'object'
          ? occupation.sinceDate
          : transformDateFromString(occupation.sinceDate, DateFormat.YYYY_MM_DD_HH_MM_SS);

      occupationValue = {
        sinceDate: occupation.sinceDate.toISOString(),
        ...occupation,
      };

      this.viewModel.modules.occupationData = occupation;

      await this.creditInteractor.saveCustomerInfo({
        moduleId: ModuleCodeEngine.OCCUPATION,
        value: occupationValue,
      });
    } catch (error) {
      const message = `(${occupation.occupation?.name}) ${error?.message}`;

      if (this.saveCustomerInfoFail(error, message)) {
        return;
      }
    }

    await this.loadingService.hideWithoutFeedback();

    this.completed.emit({
      moduleId: ModuleCodeEngine.OCCUPATION,
      value: occupationValue,
    });
  }

  private async onYourMoneyCompleted(yourMoney: YourMoneyEngineEntity) {
    await this.loadingService.show();

    try {
      this.viewModel.modules.yourMoneyData = yourMoney;
      await this.creditInteractor.saveCustomerInfo({
        moduleId: ModuleCodeEngine.INCOME_EXPENSES,
        value: this.viewModel.modules.yourMoneyData,
      });
    } catch (error) {
      if (this.saveCustomerInfoFail(error)) {
        return;
      }
    }

    await this.loadingService.hideWithoutFeedback();

    this.completed.emit({
      moduleId: ModuleCodeEngine.INCOME_EXPENSES,
      value: this.viewModel.modules.yourMoneyData,
    });
  }

  private async onValidateDocumentScanned(): Promise<BarcodeStatus> {
    try {
      if (
        this.viewModel.modules.documentScan ||
        (this.viewModel.modules.documentScan?.error &&
          this.viewModel.modules.documentScan?.error?.errorId ===
            this.config.errorCode.userDontHaveImagesError)
      ) {
        return this.viewModel.modules.documentScan;
      }

      const documentStatus = await this.documentScanInteractor.validateDocument();

      return {
        data: documentStatus,
        isSave: true,
        updatable: true,
        hasError: false,
      };
    } catch (error) {
      return this.getDocumentScannedStatusFail(error);
    }
  }

  private getDocumentScannedStatusFail(error): BarcodeStatus {
    return {
      data: null,
      isSave: false,
      updatable: true,
      hasError: true,
      error: error,
    };
  }

  private groundsForRejectionApprovalFront(error: CoreException, message: Record<string, string>) {
    message.content = createFormat(message.content, [this.i18n.creditName[this.organismId]]);

    this.approvalFrontService.showCardModal(message, () => {
      this.setAnalyticsIntentionOfRejection(error);
      this.approvalFrontService.goTo(this.config.routes.closet);
    });
  }

  private processGetApprovalAttemptError() {
    const messages = this.i18n.modalError.dontHaveApprovalAttempt;

    messages.content = createFormat(messages.content, [
      String(this.viewModel.info.attempt.maxAttempt ?? 0),
    ]);

    this.approvalFrontService.showCardModal(messages, () =>
      this.approvalFrontService.goTo(this.config.routes.closet)
    );
  }

  private setAnalyticsIntentionOfRejection(error: CoreException) {
    const eventName = `${this.i18n.creditName[this.organismId]}_${
      this.config.analytics.intentionOfRejection[error.constructor.name].name
    }`;

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: eventName,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: eventName },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: eventName,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.intentionOfRejection.params.cleverTap,
          [error.code]
        ),
      },
    });

    this.dynatraceService.setCustomError(error);
  }

  private async setAnalyticsIntentionOfBegin() {
    const eventName = `${this.config.analytics.intentionOfBegin.name}_${String(
      this.i18n.creditName[this.organismId]
    ).toLowerCase()}`;

    await this.dynatraceService.setAnalyticsOfEvent({
      eventName: eventName,
    });
  }

  private async setAnalyticsIntentionOfSucces(error?: CoreException) {
    let installment: InstallmentList;
    const eventName = `${this.config.analytics.intentionOfSucces.name}_${String(
      this.i18n.creditName[this.organismId]
    ).toLowerCase()}`;

    if (this.viewModel.info.installmentList?.length > 0) {
      installment = this.viewModel.info.installmentList[0];
    }

    await this.dynatraceService.setAnalyticsOfEvent({
      eventName: eventName,
      eventProps: stringsArrayToObjectWithValues(
        this.config.analytics.intentionOfSucces.params.dynatrace,
        [
          true,
          installment?.value ?? null,
          installment?.installment ?? null,
          error?.code ?? null,
          error?.name ?? null,
        ]
      ),
      leave: true,
    });
  }

  private showPromptAlertTimeoutApprovalFront() {
    const config = {
      i18nKey: this.config.i18n.approvalFrontTimeout,
      handlers: {
        accept: () => this.navigatorProvider.navigateRoot(this.config.routes.closet),
      },
    };

    this.alertService.showByI18n(config);
  }

  private setExceptionFactory() {
    this.factoryException = {
      [WrongDontComplyWithRiskPoliciesException.name]: (error: CoreException) =>
        this.groundsForRejectionApprovalFront(
          error,
          this.i18n.modalError.wrongDontComplyWithRiskPolicies
        ),
      [HadObligationsInMoraException.name]: (error: CoreException) =>
        this.groundsForRejectionApprovalFront(error, this.i18n.modalError.hadObligationsInMora),
      [WrongDontUserInCentralsException.name]: (error: CoreException) =>
        this.groundsForRejectionApprovalFront(error, this.i18n.modalError.wrongDontUserInCentrals),
      [WrongMeetsPaymentCapacityException.name]: (error: CoreException) =>
        this.groundsForRejectionApprovalFront(
          error,
          this.i18n.modalError.wrongMeetsPaymentCapacity
        ),
      [HaveApprovalAttemptException.name]: (error: CoreException) => this.onSuccesEmit(error),
      [DontHaveApprovalAttemptWithDataException.name]: (error: CoreException) =>
        this.onSuccesEmit(error),
      [DontHaveApprovalAttemptException.name]: () => this.processGetApprovalAttemptError(),
      [TimeoutApprovalFrontException.name]: () => this.showPromptAlertTimeoutApprovalFront(),
      [ValidateDataDoesNotMatchException.name]: () => this.dataDoesNotMatchError(),
    };
  }

  private saveCustomerInfoFail(error, message?): boolean {
    this.dynatraceService.setCustomError(error, message);
    this.loggerInteractor.error(error);

    if (this.factoryException[error.constructor.name]) {
      return this.factoryException[error.constructor.name]();
    }
  }

  private dataDoesNotMatchError(): boolean {
    const messages = this.i18n.modalError.dataDoesNotMatch;

    this.approvalFrontService.showCardModal(messages, () => this.sessionProvider.closeApp());

    return true;
  }
}
