import { FormControl, FormGroup, Validators } from '@angular/forms';
import { YourMoneyEngineEntity } from '@nequi/features/credits/core';

export class YourMoneyEngineViewModel {
  public form: FormGroup;
  public incomes: FormControl;
  public expenses: FormControl;
  public otherIncomes: FormControl;
  public canShowAdditionalIncomesField: boolean;
  public minAmount: number;
  public maxAmount: number;

  public setCeilings(min: number, max: number) {
    this.minAmount = min;
    this.maxAmount = max;
  }

  public async initForm(
    data: YourMoneyEngineEntity,
    canShowAdditionalIncomesField: boolean
  ): Promise<void> {
    this.canShowAdditionalIncomesField = canShowAdditionalIncomesField;

    this.incomes = new FormControl(data?.incomes ?? 0, [
      Validators.required,
      Validators.min(this.minAmount),
      Validators.max(this.maxAmount),
    ]);

    this.expenses = new FormControl(data?.expenses ?? 0, [
      Validators.required,
      Validators.min(this.minAmount),
      Validators.max(this.maxAmount),
    ]);

    this.otherIncomes = new FormControl(data?.otherIncomes ?? 0, [
      Validators.required,
      Validators.min(this.minAmount),
      Validators.max(this.maxAmount),
    ]);

    this.form = new FormGroup({
      incomes: this.incomes,
      expenses: this.expenses,
      otherIncomes: this.otherIncomes,
    });
  }
}
