import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { CoreException } from '@nequi/commons/exceptions';
import { FlowType } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { RescheduleType } from '@nequi/features/credits/core';
import { OriginMoneyControlComponent } from '@nequi/features/money-management/presentation';

import { AdjustInstallmentsConfig } from './adjust-installments.config';
import { AdjustInstallmentViewModel } from './adjust-installments.view-model';

export interface AdjustInstallmentsEntity {
  extraMoney: number;
  amountFeesAdjusted: number;
  lastFeeDate: Date;
  numberFeesAdjusted: number;
  nextFeeDate: Date;
}

@Component({
  selector: 'app-adjust-installment',
  templateUrl: './adjust-installments.organism.html',
  styleUrls: ['./adjust-installments.organism.scss'],
})
export class AdjustInstallmentsOrganism extends FeatureBasePage {
  @ViewChild(OriginMoneyControlComponent)
  public originMoneyControl: OriginMoneyControlComponent;

  @Input() public title: string;
  @Input() public data: AdjustInstallmentsEntity;
  @Input() public flow: FlowType;
  @Output() public accept = new EventEmitter<{
    isSuccess: boolean;
    rescheduleType?: RescheduleType;
    error?: CoreException;
  }>();

  public viewModel = new AdjustInstallmentViewModel();
  public config = AdjustInstallmentsConfig;
  public flowType = FlowType;

  public async onAccept(): Promise<void> {
    this.accept.emit({ isSuccess: true, rescheduleType: this.viewModel.rescheduleType.value });
  }
}
