import { AfterViewChecked, ChangeDetectorRef, Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoggerInteractor, ParameterInteractor } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import {
  CreditInteractor,
  GetOccupationsEntity,
  OccupationEngineEntity
} from '@nequi/features/credits/core';
import { LoadingService } from '@nequi/ui';

import { OccupationEngineService } from '../../services/occupation-engine.service';
import { OccupationEngineConfig } from './occupation-engine.config';
import { OccupationEngineViewModel } from './occupation-engine.view-model';

@Component({
  selector: 'app-credits-occupation-engine',
  templateUrl: './occupation-engine.page.html',
})
export class OccupationEnginePage extends FeatureBasePage implements OnInit, AfterViewChecked {
  public config: Record<string, any> = OccupationEngineConfig;
  public viewModel: OccupationEngineViewModel;
  public occupationData: OccupationEngineEntity;

  constructor(
    protected readonly injector: Injector,
    private router: Router,
    private occupationEngineService: OccupationEngineService,
    private changeDetectorRef: ChangeDetectorRef,
    private creditInteractor: CreditInteractor,
    private loggerInteractor: LoggerInteractor,
    private parameterInteractor: ParameterInteractor,
    private loadingService: LoadingService
  ) {
    super(injector);
    this.viewModel = new OccupationEngineViewModel();
  }

  public ngOnInit() {
    this.initData();
  }

  public ngAfterViewChecked(): void {
    this.changeDetectorRef.detectChanges();
  }

  public onComplete(): void {
    this.occupationEngineService.emit(this.viewModel.form.value);
  }

  public get maxDate() {
    const currentDate = new Date();
    const maxDate = currentDate.setDate(currentDate.getDate() + 1);

    return new Date(maxDate).toISOString();
  }

  private async initData() {
    try {
      this.loadingService.show();
      this.occupationData = this.router.getCurrentNavigation().extras
        .state as OccupationEngineEntity;

      this.viewModel.initForm(this.occupationData);
      this.viewModel.validateOccupation(this.occupationData?.occupation);

      const parameters = await this.parameterInteractor.getParameter<GetOccupationsEntity>(
        OccupationEngineConfig.dynamoParam
      );

      this.viewModel.setOccupationsAndContracts(
        await this.creditInteractor.getOccupations(parameters)
      );
    } catch (error) {
      this.loggerInteractor.error(error);
    } finally {
      this.loadingService.hideWithoutFeedback();
    }
  }
}
