import { Component, Input, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { BottomSheetController, Theme } from '@nequi/ui';

import { AddMoreInfoProps } from './entities/add-more-info-props.interface';

@Component({
  selector: 'app-add-more-info',
  templateUrl: './add-more-info.organism.html',
  styleUrls: ['./add-more-info.organism.scss'],
})
export class AddMoreInfoOrganism implements OnInit, AddMoreInfoProps {
  @Input()
  public title?: string;

  @Input()
  public label?: string;

  @Input()
  public theme?: string = Theme.LIGHT;

  @Input()
  public actionLabel?: string;

  @Input()
  public action?: (form: FormGroup) => void;

  public readonly THEMES = Theme;
  public form: FormGroup;
  public moreInfo: FormControl;

  constructor(public bottomSheetController: BottomSheetController) {}

  public ngOnInit(): void {
    this.initForm();
  }

  public hide() {
    this.bottomSheetController.dismiss();
  }

  public initForm(): void {
    this.moreInfo = new FormControl(null, [Validators.required]);
    this.form = new FormGroup({
      moreInfo: this.moreInfo,
    });
  }
}
