import { FormControl, FormGroup, Validators } from '@angular/forms';
import {
  ContractDetailEntity,
  GetOccupationsEntity,
  OccupationDetailEntity,
  OccupationEngineEntity
} from '@nequi/features/credits/core';

import { OccupacionEngineTypes } from './constants/occupation-engine-types';

const CONTRACT_CODE_DEFAULT = '01';

export class OccupationEngineViewModel {
  public form: FormGroup;
  public occupation: FormControl;
  public sinceDate: FormControl;
  public contract: FormControl;
  public showSinceDateField: boolean;
  public showContractField: boolean;
  public occupationList: OccupationDetailEntity[];
  public contractList: ContractDetailEntity[];

  public initForm(data: OccupationEngineEntity): void {
    this.occupation = new FormControl(data?.occupation ?? '', [Validators.required]);
    this.sinceDate = new FormControl(data?.sinceDate ?? '', [Validators.required]);
    this.contract = new FormControl(data?.contract ?? { code: CONTRACT_CODE_DEFAULT, name: '' }, [
      Validators.required,
    ]);

    this.form = new FormGroup({
      occupation: this.occupation,
      sinceDate: this.sinceDate,
      contract: this.contract,
    });
  }

  public setOccupationsAndContracts(data: GetOccupationsEntity) {
    this.contractList = data.contractList;
    this.occupationList = data.occupationList;
  }

  public validateOccupation(value: OccupationDetailEntity): void {
    if (value?.type === OccupacionEngineTypes.RETIRED) {
      this.showSinceDateField = false;
      this.showContractField = false;
    }

    if (value?.type === OccupacionEngineTypes.INDEPENDENT) {
      this.showSinceDateField = true;
      this.showContractField = false;
    }

    if (value?.type === OccupacionEngineTypes.EMPLOYEE) {
      this.showSinceDateField = true;
      this.showContractField = true;
    }
  }

  public resetForm(value: OccupationDetailEntity): void {
    this.validateOccupation(value);

    this.form.patchValue({
      sinceDate: new Date(),
      contract: { code: CONTRACT_CODE_DEFAULT, name: '' },
    });
  }

  public isValidForm(): boolean {
    if (!this.showSinceDateField) {
      return this.occupation.valid;
    }

    if (!this.showContractField) {
      return this.occupation.valid && this.sinceDate.valid;
    }

    return this.form.valid;
  }
}
