import { CurrencyPipe } from '@angular/common';
import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild
} from '@angular/core';
import { CoreException } from '@nequi/commons/exceptions';
import { DynatraceService } from '@nequi/features/commons/presentation/services';
import { TranslateProvider } from '@nequi/features/commons/providers';
import {
  CreditEntity,
  CreditInteractor,
  CustomerCreditsEntity,
  DontHaveApprovalAttemptException,
  DontHaveApprovalAttemptWithDataException,
  GetApprovalAttemptEntity,
  HaveApprovalAttemptException,
  NeverBeenApprovedFrontException,
  PreapprovedEntity,
  PreapprovedParams,
  ValidateCreditsParamsException
} from '@nequi/features/credits/core';
import { InputSelectControlComponent, ViewDetailStatus } from '@nequi/ui';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { CustomerFinancialService } from '../../services/customer-finacial.service';
import { ApprovalFrontViewModel } from '../approval-front/approval-front.view-model';
import { ApprovalFrontService } from '../approval-front/services/approval-front.service';
import { PreapprovalConfig } from './preapproval.config';
import { PreapprovalViewModel } from './preapproval.view-model';
import { PreapprovedService } from './services/preapproved.service';

interface PropellerMessages {
  termLabel: string;
  monthlyPaymentAmount: {
    label: string;
    complementText: string;
    additionalText: string;
    infoMessage: string;
  };
}
interface PreapprovalDetail {
  detail: {
    subtitle: string;
    monthlyPayment: string;
  };
}

interface PreapprovalMessages extends Partial<PropellerMessages>, Partial<PreapprovalDetail> {
  headerTitle: string;
  amount: {
    title: string;
    min: string;
  };
  footer: {
    readyButtonText: string;
    conditionButtonText?: string;
    authorization?: string;
  };
}

const DEFAULT_TIMEOUT = 500;

@Component({
  selector: 'app-preapproval',
  templateUrl: './preapproval.organism.html',
  styleUrls: ['./preapproval.organism.scss'],
})
export class PreapprovalOrganism implements OnInit, OnDestroy {
  @ViewChild(InputSelectControlComponent) public installmentSelect: InputSelectControlComponent;
  @Input() public organismId: string;
  @Input() public customerCredits: CustomerCreditsEntity;
  @Input() public financingAmount;
  @Input() public operationParams?: PreapprovedParams<number>;
  @Input() public showViewDetail? = false;
  @Input() public messages?: PreapprovalMessages;
  @Input() public showHelpButton? = false;
  @Input() public showApprovalFront? = false;

  @Output() public nextButtonCliked = new EventEmitter();
  @Output() public goBackCliked = new EventEmitter();
  @Output() public showHelpPressed = new EventEmitter();

  public credit: CreditEntity;
  public preapproved: PreapprovedEntity;
  public config = PreapprovalConfig;
  public preapprovedLabel: string;
  public detailColor = ViewDetailStatus.GRAY;
  private factoryException = {};

  constructor(
    private translateProvider: TranslateProvider,
    private creditInteractor: CreditInteractor,
    public preapprovedService: PreapprovedService,
    public viewModel: PreapprovalViewModel,
    public approvalFrontViewModel: ApprovalFrontViewModel,
    private currencyPipe: CurrencyPipe,
    private approvalFrontService: ApprovalFrontService,
    private dynatraceService: DynatraceService,
    private customerFinancialService: CustomerFinancialService
  ) {}

  public async ngOnInit() {
    await this.setAnalyticsPreapproved();

    this.onGetOperationParams();
    this.approvalFrontService.subscribe(this.initPage.bind(this));
  }

  public async ngOnDestroy() {
    this.approvalFrontService.unsubscribe();
  }

  public goBack() {
    this.goBackCliked.emit();
  }

  public goToHelp() {
    this.showHelpPressed.emit();
  }

  public async goToPaymentGuarantees() {
    this.viewModel.setPaymentGuaranteesInfo();
    this.nextButtonCliked.emit();
  }

  public async onSpecialConditions() {
    this.getApprovalUpFrontAttempt();
  }

  public async onChangeInstallment(event): Promise<void> {
    if (
      this.approvalFrontViewModel.info.attempt.hasAttempts &&
      this.viewModel.preapproved.maxInstallment !== event.value
    ) {
      if (!(await this.approvalUpFrontLastAttemptError(event))) {
        this.setCurrentInstallment(event);
      }

      return;
    }

    this.viewModel.changeInstallment(event);
  }

  public setExceptionFactory() {
    this.factoryException = {
      [HaveApprovalAttemptException.name]: (error: CoreException) => {
        this.processHaveApprovalAttempt(error);
      },
      [DontHaveApprovalAttemptWithDataException.name]: (
        error: CoreException<GetApprovalAttemptEntity>
      ) => {
        this.processDontHaveApprovalAttemptWithDataError(error);
      },
      [DontHaveApprovalAttemptException.name]: (error: CoreException) => {
        this.processDontHaveApprovalAttemptError(error);
      },
      [NeverBeenApprovedFrontException.name]: () => {
        this.processNeverBeenApprovedFrontError();
      },
    };
  }

  public async getApprovalUpFrontAttempt(event?): Promise<void> {
    event?.stopPropagation();
    this.viewModel.currentTerm = this.viewModel.term.value;

    if (this.approvalFrontViewModel.info.isAvailable) {
      try {
        if (!this.approvalFrontViewModel.info.attempt.isCallService) {
          this.preapprovedService.loadingService.show();
          this.approvalFrontViewModel.info.attempt.hasAttempts = false;

          const response = (await this.creditInteractor.getApprovalAttempt()) as GetApprovalAttemptEntity;

          if (response) {
            this.approvalFrontViewModel.info.attempt.maxAttempt =
              response?.attempts?.maxAttempt ?? null;
          }

          this.approvalFrontViewModel.info.attempt.isCallService = true;
          this.approvalFrontViewModel.info.attempt.hasAttempts = true;
          this.preapprovedService.loadingService.hideWithoutFeedback();
        }

        if (this.approvalFrontViewModel.info.attempt.error) {
          this.dynatraceService.setCustomError(this.approvalFrontViewModel.info.attempt.error);
          throw this.approvalFrontViewModel.info.attempt.error;
        }

        this.processHaveApprovalAttempt();
      } catch (error) {
        this.failApprovalUpFrontAttempt(error);
      }
    }
  }

  public updateDetails() {
    const monthlyFee = this.viewModel.getMonthlyFee();

    this.viewModel.details[0].description = this.currencyPipe.transform(
      monthlyFee - this.viewModel.financingAmount.value
    );

    this.viewModel.details[1].description = this.currencyPipe.transform(monthlyFee);
  }

  public async bussyIndicatorReload() {
    await this.onGetOperationParams();
  }

  private async setCurrentInstallment(event?) {
    this.approvalFrontViewModel.info.currentInstallment = event;
    this.viewModel.term.setValue(this.viewModel.currentTerm);

    await this.setAnalyticsIntentionOfApprovalUpFront();
    this.preapprovedService.goToSpecialConditions();
  }

  private async approvalUpFrontLastAttemptError(event?): Promise<boolean> {
    if (
      this.approvalFrontViewModel.info.attempt.error?.constructor.name ===
      HaveApprovalAttemptException.name
    ) {
      const messages = this.translateProvider.get(this.config.i18n.modal.lastApprovalAttempt);

      await this.approvalFrontService.showCardModal<boolean>(
        messages,
        () => this.setCurrentInstallment(event),
        () => this.viewModel.term.setValue(this.viewModel.currentTerm)
      );

      return true;
    }
  }

  private async failApprovalUpFrontAttempt(error: CoreException) {
    this.dynatraceService.setCustomError(error);

    this.preapprovedService.loadingService.hideWithoutFeedback();

    if (this.factoryException[error.constructor.name]) {
      this.factoryException[error.constructor.name](error);

      this.approvalFrontViewModel.info.attempt.isCallService = true;
      this.approvalFrontViewModel.info.attempt.error = error;

      return;
    }

    await this.preapprovedService.toastService.showError(error.message);
  }

  private async processHaveApprovalAttempt(error?: CoreException) {
    let message = this.translateProvider.get(this.config.i18n.toast.haveApprovalAttempt);
    const param = String(
      this.approvalFrontViewModel.info.attempt.maxAttempt ??
        (error.data as GetApprovalAttemptEntity).attempts.maxAttempt
    );

    this.approvalFrontViewModel.info.attempt.hasAttempts = true;
    message = createFormat(message, [param]);

    this.viewModel.setTermOptionSelect();
    this.installmentSelect.options = this.viewModel.termOptionsSelect;
    await this.installmentSelect.showOptions();

    setTimeout(async () => {
      await this.preapprovedService.toastService.showInfo(message);
    }, DEFAULT_TIMEOUT);
  }

  private async processNeverBeenApprovedFrontError() {
    this.approvalFrontViewModel.info.attempt.hasAttempts = true;

    this.viewModel.setTermOptionSelect();
    this.installmentSelect.options = this.viewModel.termOptionsSelect;
    await this.installmentSelect.showOptions();
  }

  private async processDontHaveApprovalAttemptWithDataError(
    error: CoreException<GetApprovalAttemptEntity>
  ) {
    await this.viewModel.setTermOptionsWithInstallment(error.data.installmentList);
    this.installmentSelect.options = this.viewModel.termOptionsSelect;
    await this.installmentSelect.showOptions();

    const message = this.translateProvider.get(
      this.config.i18n.toast.dontHaveApprovalAttemptWithData
    );

    setTimeout(async () => {
      await this.preapprovedService.toastService.showInfo(message);
    }, DEFAULT_TIMEOUT);
  }

  private async processDontHaveApprovalAttemptError(error: CoreException) {
    const messages = this.translateProvider.get(this.config.i18n.modal.dontHaveApprovalAttempt);

    messages.description = this.translateProvider.get(
      this.config.i18n.modal.dontHaveApprovalAttemptDescription,
      {
        limitedConsultations: String((error.data as GetApprovalAttemptEntity).attempts.maxAttempt),
      }
    );

    this.approvalFrontService.showCardModal(messages);
  }

  private async initPage() {
    this.setExceptionFactory();
    this.viewModel.initForm(this.financingAmount);
    await this.viewModel.processPreapproved(this.customerCredits);

    this.preapprovedLabel = !this.viewModel.isMinAmountPreaproved
      ? this.messages.amount.title
      : this.messages.amount.min;

    if (this.showViewDetail) {
      this.loadDetails();
    }
  }

  private loadDetails() {
    const message = this.translateProvider.get(this.messages.detail.monthlyPayment);

    this.viewModel.buildDetail(message);
    this.updateDetails();
  }

  private async setAnalyticsIntentionOfApprovalUpFront() {
    const creditName = this.translateProvider.get(this.config.i18n.creditName);
    const eventName = `${this.config.analytics.intentionOfApprovalUpFront.name}_${String(
      creditName[this.organismId]
    ).toLowerCase()}`;

    const eventProps = stringsArrayToObjectWithValues(
      this.config.analytics.intentionOfApprovalUpFront.params.dynatrace,
      [this.customerCredits.preapproved.product.id, this.viewModel.maxAmount]
    );

    await this.dynatraceService.setAnalyticsOfEvent({ eventName, eventProps, leave: true });
  }

  private async onGetOperationParams() {
    try {
      await this.preapprovedService.loadingService.show();

      const response = await this.creditInteractor.getOperationParams(
        this.customerCredits.preapproved.product.id
      );

      this.approvalFrontViewModel.info.isAvailable = this.showApprovalFront
        ? response?.isAvailableApprovalFront
        : false;

      this.viewModel.operationParams = this.operationParams ?? response?.preapproved;

      this.viewModel.bussyIndicator.show = false;
      this.viewModel.bussyIndicator.error = '';

      this.initPage();
    } catch (error) {
      let message = error.message;

      if (error.constructor.name === ValidateCreditsParamsException.name) {
        message = this.translateProvider.get(this.config.i18n.bussyIndicator);
      }

      this.viewModel.bussyIndicator.show = true;
      this.viewModel.bussyIndicator.error = message;

      this.dynatraceService.setCustomError(error);
    } finally {
      await this.preapprovedService.loadingService.hideWithoutFeedback();
    }
  }

  private async setAnalyticsPreapproved() {
    const creditName = this.translateProvider.get(this.config.i18n.creditName);
    const eventName = `${this.config.analytics.intentionOfPreapproved.name}_${String(
      creditName[this.organismId]
    ).toLowerCase()}`;

    const eventProps = stringsArrayToObjectWithValues(
      this.config.analytics.intentionOfPreapproved.params.dynatrace,
      [
        this.customerCredits.preapproved.product.id,
        this.customerFinancialService.creditUserInfo?.hasPreapproved.toString(),
      ]
    );

    await this.dynatraceService.setAnalyticsOfEvent({ eventName, eventProps });
  }
}
