import { Component, EventEmitter, Injector, Input, OnInit, Output } from '@angular/core';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { ModuleConfigProvider, TranslateProvider } from '@nequi/features/commons/providers';
import { CreditMovementsInteractor } from '@nequi/features/credits/core';
import { OriginMoneyService } from '@nequi/features/money-management/presentation';
import { CardModalService, ProgressBarType } from '@nequi/ui';

import { PaymentConfig } from './payment.config';
import { PaymentType } from './payment.type';
import { PaymentViewModel } from './payment.view-model';

interface PaymentMessages {
  payment: {
    headerTitle: string;
    toPay: string;
    fee: string;
    nextFee: string;
    nextFeeAmount: string;
    overdueFee: string;
    paymentHelp: string;
    payTodayFeeHelpChecked: string;
    payTodayFeeHelpUnchecked: string;
    payOverdueFeeHelpChecked: string;
    payOverdueFeeHelpUnchecked: string;
    payment: string;
    payTodayFee: string;
    payOverdueFee: string;
    paySomething: string;
    payComplete: string;
    footer: {
      message: string;
      readyButtonText: string;
      movements: {
        label: string;
        cardModal: string;
      };
    };
    pocketPlaceholder?: string;
  };
}

@Component({
  selector: 'app-payment',
  templateUrl: './payment.organism.html',
  styleUrls: ['./payment.organism.scss'],
})
export class PaymentOrganism extends FeatureBasePage implements OnInit {
  @Input() public organismId: string;
  @Input() public messages: PaymentMessages;
  @Input() public feePaymentMessage: string;
  @Input() public showMovements? = false;
  @Input() public showPaymentProgress? = true;
  @Input() public showAmountNextInstallment? = true;
  @Input() public config = PaymentConfig;
  @Input() public showHelpButton? = false;

  @Output() public showDetailPressed = new EventEmitter();
  @Output() public goToPaymentPressed = new EventEmitter();
  @Output() public goToBackPressed = new EventEmitter();
  @Output() public showHelpPressed = new EventEmitter();

  public PaymentType = PaymentType;
  public progressBarType = ProgressBarType;
  public clickStatus = true;
  public progressBar = {
    value: 0,
    type: this.progressBarType.DETERMINATE,
    cssClass: this.config.percentage.p100.cssClass,
  };

  private shouldShowModal: boolean;

  constructor(
    public viewModel: PaymentViewModel,
    private applicationDataInteractor: ApplicationDataInteractor,
    private cardModalService: CardModalService,
    private translateProvider: TranslateProvider,
    private creditMovementsInteractor: CreditMovementsInteractor,
    private moduleConfigProvider: ModuleConfigProvider,
    private originMoneyService: OriginMoneyService,
    injector: Injector
  ) {
    super(injector);
  }

  public ngOnInit() {
    this.viewModel.clickStatus = this.clickStatus;
    this.getPaymentPercentage();
    this.setPocket();
  }

  public goBack() {
    this.goToBackPressed.emit();
  }

  public goToHelp() {
    this.showHelpPressed.emit();
  }

  public goToDetail() {
    this.showDetailPressed.emit();
  }

  public goToPay() {
    this.goToPaymentPressed.emit();
  }

  public checkPayFee() {
    this.viewModel.checkPayFee();
  }

  public checkPayComplete() {
    this.viewModel.checkPayComplete();
  }

  public async onShowMovements() {
    this.shouldShowModal = await this.applicationDataInteractor.findValue<boolean>(
      this.moduleConfigProvider.applicationData.showMovementsModal,
      true
    );

    this.creditMovementsInteractor.setDebenture(this.viewModel.credit.debenture);

    const titleCreditMovements = this.config.i18n.movementsTitle[this.viewModel.credit.type];

    if (this.shouldShowModal) {
      await this.applicationDataInteractor.save(
        this.moduleConfigProvider.applicationData.showMovementsModal,
        false
      );

      await this.showMovementCreditsModal(titleCreditMovements);

      return;
    }

    this.navigatorProvider.navigateForward(this.config.routes.movements, {
      state: { title: titleCreditMovements },
    });
  }

  private async showMovementCreditsModal(titleCreditMovements: string) {
    const modal = this.translateProvider.get(this.messages.payment.footer.movements.cardModal);

    await this.showCardModal(modal, () =>
      this.navigatorProvider.navigateForward(this.config.routes.movements, {
        state: { title: titleCreditMovements },
      })
    );
  }

  private async showCardModal(messages: any, handler: () => void) {
    await this.cardModalService.show({
      title: messages.title,
      imgPath: messages.image,
      content: messages.content,
      buttons: [
        {
          text: messages.buttons.accept,
          handler,
        },
      ],
    });
  }

  private getPaymentPercentage() {
    const value =
      this.viewModel.credit.installment.totalPaid / this.viewModel.credit.installment.total;

    this.progressBar.value = value;
    this.progressBar.cssClass = this.getProgressPercent(value);
  }

  private getProgressPercent(value: number) {
    if (value <= this.config.percentage.p25.value) {
      return this.config.percentage.p25.cssClass;
    }

    if (value > this.config.percentage.p25.value && value <= this.config.percentage.p50.value) {
      return this.config.percentage.p50.cssClass;
    }

    if (value > this.config.percentage.p50.value && value <= this.config.percentage.p75.value) {
      return this.config.percentage.p75.cssClass;
    }

    return this.config.percentage.p100.cssClass;
  }

  private setPocket() {
    this.viewModel.pocketControl.setValue(this.originMoneyService.getAvailablePocket());
  }
}
