import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ObservableBaseService } from '@nequi/features/commons';
import { NavigatorProvider } from '@nequi/features/commons/providers';
import { CardModalService, LoadingService, ModalButton, ToastService } from '@nequi/ui';
import { openUrl } from '@nequi/utils/functions';

import { ApprovalFrontConfig } from '../approval-front.config';

@Injectable()
export class ApprovalFrontService extends ObservableBaseService<boolean> {
  private config = ApprovalFrontConfig;

  constructor(
    public loadingService: LoadingService,
    public toastService: ToastService,
    public cardModalService: CardModalService,
    private navigatorProvider: NavigatorProvider,
    private route: ActivatedRoute
  ) {
    super();
  }

  public async showCardModal<T = void>(
    message: Record<string, any>,
    onAccept?: Function,
    onCancel?: Function
  ): Promise<T> {
    const buttons: ModalButton[] = [
      {
        text: message.buttons.accept,
        handler: async () => onAccept?.(),
      },
    ];

    if (message.buttons?.cancel) {
      buttons.push({
        text: message.buttons.cancel,
        color: this.config.colors.midnight,
        handler: async () => onCancel?.(),
      });
    }

    if (message.buttons?.link) {
      buttons.push({
        isLink: true,
        text: message.buttons.link.text,
        handler: () => {
          openUrl(message.buttons.link.url);
        },
      });
    }

    const result = await this.cardModalService.show({
      title: message.title,
      imgPath: message.image,
      content: message?.content ?? message?.description,
      buttons: buttons,
    });

    return result?.data as Promise<T>;
  }

  public async goTo(url: string) {
    this.navigatorProvider.navigateRoot(url, {
      relativeTo: this.route,
      replaceUrl: true,
    });
  }
}
