import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NavController } from '@ionic/angular';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { CardModalService, FullscreenModalService, LoadingService, ToastService } from '@nequi/ui';
import { toCamelCaseProperties } from '@nequi/utils/functions';

import { PreapprovalConfig } from '../preapproval.config';

@Injectable()
export class PreapprovedService {
  private config = PreapprovalConfig;

  constructor(
    private fullscreenModalService: FullscreenModalService,
    private navController: NavController,
    private route: ActivatedRoute,
    private translateProvider: TranslateProvider,
    public loadingService: LoadingService,
    public toastService: ToastService,
    public cardModalService: CardModalService
  ) {}

  public async showFullScreenModal(i18n: Record<string, string>): Promise<void> {
    const i18nFormatted = toCamelCaseProperties<Record<string, string>>(i18n);

    await this.fullscreenModalService.show({
      title: i18nFormatted.title,
      imgPath: i18nFormatted.image,
      content: i18nFormatted.description,
      cssClass: this.config.fullscreenModal.cssClass,
      buttons: [
        {
          text: i18nFormatted.acceptButton,
          handler: this.goToKnowUsBetterEngine.bind(this),
          cssClass: this.config.fullscreenModal.cssClassButton,
        },
      ],
      link: {
        text: i18nFormatted.moreInfoLink,
        url: this.config.routes.moreInfo,
      },
    });
  }

  public async goToKnowUsBetterEngine() {
    this.navController.navigateForward(this.config.routes.knowUsBetterEngine, {
      relativeTo: this.route,
    });
  }

  public async goToSpecialConditions() {
    const messages = this.translateProvider.get(this.config.i18n.modal.specialConditions);

    this.showFullScreenModal(messages);
  }

  public goBack() {
    this.navController.back();
  }

  public async showCardModal(i18n: Record<string, string>): Promise<void> {
    this.cardModalService.show({
      title: i18n.title,
      imgPath: i18n.image,
      content: i18n.content,
      buttons: [
        {
          text: i18n.readyButton,
          handler: () => this.navController.back(),
        },
      ],
    });
  }
}
